.popup_menu {
    // display: none;
    overflow-x: hidden;
    // height: 100%;
    height: 0%;
    width: 100%;
    position: fixed;
    z-index: 4;
    // background-color: rgba(0,0,0, 0.9);
    background: #a11c21;
    // animation-name: ease;
    transition: height 0.5s;
}

.menu_link {
    position: relative;
    top: 25%;
    width: 100%;
    text-align: center;
}

.link_text {
    display: block;
    text-decoration: none;
    font-size: 50px;
    margin-top: 10px;
    color: #ffb105;
}

.link_text:hover, .link_text:focus, .popup_menu .close_btn:hover, .popup_menu .close_btn:focus {
    text-decoration: none;
    color: #f1f1f1;
}

.libraries {
    display: none;
}

.libraries h1 {
    font-size: 50px;
    color: white;
}

.libraries a {
    display: block;
    font-size: 30px;
}

.libraries hr {
    height: 10px;
    border: 0;
    box-shadow: 0 10px 10px -10px #3a3a3a inset;
}

.popup_menu .close_btn {
    position: absolute;
    top: 20px;
    right: 3%;
    font-size: 60px;
    color: #ffb105;
	cursor: pointer;
}

#login_instruction {
	cursor: pointer;
    font-size: 41px;
}

#epic_instruction, #destiny_instruction {
    display: none;
    font-size: 30px;
    margin-top: -5px;
}

@media screen and (max-width: 600px) {
    /* CSS for screens that are 600 pixels or less will be put in this section */
    .menu_link {
        top: 5%
    }

    .libraries {
        display: block;
    }
}

@media screen and (max-width: 450px) {
    .link_text, #login_instruction {
        font-size: 35px
    }
    .popup_menu .close_btn {
        font-size: 40px;
        right: 35px;
    }
}
