.footer_body {
    display: block;
    width: 100%;
    height: 100%;
    padding-top: 10px;
    background-color: #f0f1ec;
    color: #464644;
}

.upper_footer {
    margin: 50px 80px 230px 80px;
}

.footer_column {
    float: left;
}


// coloumn 1
.footer_logo {
    text-align: left;
    width: 40%;
}

.affiliated {
    margin-top: 40px;
}

.affiliated div {
    display: inline-block;
    vertical-align: middle;
}

.cism_footer_logo {
    margin-left: 30px;
    width: 48px;
}

.ib_footer_logo {
    margin-left: 30px;
    width: 42px;
}

.deped_footer_logo {
    margin-left: 30px;
    width: 80px;
}


// coloumn 2
.online_libraries {
    width: 35%;
    float: right;
}

.online_libraries div{
    display: inline-block;
    vertical-align: middle;
}

.destiny_footer_link a img{
    width: 60%;
    margin-right: -50px;
}


// coloumn 3
.contact_details {
    text-align: left;
    width: 25%;
}

.address div, .contact div, .email div{
    display: inline-block;
    vertical-align: top;
}

.address_icon, .contact_icon, .email_icon {
    margin-right: 10px;
}



.bottom_most_footer {
    display: block;
    overflow: hidden;
    background-color: #464445;
    width: 100%;
}

.social_medias div {
    display: inline-block;
    vertical-align: top;
}

.social_medias div a {
    color: #f0f1ec;

}

.social_medias div a:hover {
    text-decoration: none;
}

.icon {
    margin: 10px 25px;
}

@media screen and (max-width: 1120px) {
    /*screen <= 1120px*/

    .upper_footer {
        margin: 50px 50px 230px 50px;
    }
}

@media screen and (max-width: 1050px) {
    /*screen <= 1030px*/

    .upper_footer {
        margin: 50px 30px 20px 30px;
    }
    
    .footer_logo, .online_libraries {
        width: 50%;
    }
    
    .contact_details {
        text-align: center;
        width: 100%;
    }

    .contact_details div {
        display: inline-block;
        vertical-align: top;
        text-align: left;
        margin: 5px 5px 5px 5px;
    }
}

@media screen and (max-width: 785px) {
    /*screen <= 785px*/
    
    .footer_logo, .online_libraries {
        text-align: center;
        width: 100%;
        margin-bottom: 20px;
    }
}

@media screen and (max-width: 421px) {
    /*screen <= 421px*/

    .affiliated {
        margin-top: 20px;
    }

    .affiliated .affiliated_text {
        display: block;
        text-align: left;
    }
}
