.activity {
    position:relative;
    overflow:hidden;
    background: #f1f1f1;
    // height: 100vh;
    padding-top: 50px;
    padding-bottom: 50px;
}

.activity_header h1 {
    margin-bottom: -10px;
    font-size: 330%;
    color: #262626;
}

.activity_header h6 {
    margin-left: 130px;
    font-size: 150%;
    font-weight: normal;
    color: #d4880e;
}

.padlet_picture {
    filter: drop-shadow(0px 0px 10px rgba(31, 31, 31, 0.5));
}

.padlet_picture img {
    position: relative;
    float: left;
    margin-top: 250px;
    margin-left: 10px;
    width: 25%;
    clip-path: circle(41% at 50% 50%);
}

.padlet_embed {
    float: right;
    width: 68%;
    height: 60vw;
    max-width: 100%;
    margin-top: 50px;
    margin-right: 50px;
    box-shadow: 0 3px 7px 3px rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.padlet_iframe {
    width:100%;
    height:100%;
    display:block;
    padding:0;
    margin:0;
}

@media screen and (max-width: 860px) {
    /* CSS for screens that is less than or equal to 860px will be put in this section */
    .padlet_picture img {
        position: relative;
        display: block;
        clip-path: none;
        margin: 25px 15%;
        width: 70%;
        height: 40vw;
        box-shadow: 0 3px 7px 3px rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }
    .padlet_embed {
        position: relative;
        display: block;
        margin: 25px 5%;
        width: 90%;
        height: 100vw;
    }
    .activity {
        height: 180vw;
    }
}

@media screen and (min-width: 381px) and (max-width: 565px) {
    .activity {
        height: 200vw;
    }
}

@media screen and (max-width: 380px) {
    .activity {
        height: 215vw;
    }
}
