.contact_us {
    padding-top: 110px;
    padding-bottom: 5%;
}

.contact_us_header h1 {
    font-size: 400%;
    margin-bottom: -10px;
}
.contact_us_header h6 {
    font-weight: normal;
    font-size: 150%;
    margin-left: 200px;
    color: #d4880e;    
}

.contact_us_form_iframe {
    height: 700px;
    overflow-x: hidden;
}

#map-container {
    margin-top: 5%;
}

.contact_us_map_iframe {
    border:0;
}

.round-button {
    border-radius: 50%;
}

@media screen and (max-width: 1200px) {
    /* CSS for screens that are 1200px or less will be put in this section */

    .contact_us_form_iframe {
        height: 720px;
    }
}
@media screen and (max-width: 991px) {
    /* CSS for screens that are 991px or less will be put in this section */

    .contact_us_form_iframe {
        height: 780px;
    }
}
@media screen and (max-width: 767px) {
    /* CSS for screens that are 767px or less will be put in this section */

    .contact_us_form_iframe {
        height: 720px;
    }
}
@media screen and (max-width: 510px) {
    /* CSS for screens that are 510px or less will be put in this section */

    .contact_us_form_iframe {
        height: 740px;
    }
}
@media screen and (max-width: 428px) {
    /* CSS for screens that are 428px or less will be put in this section */

    .contact_us_form_iframe {
        height: 780px;
    }
}
@media screen and (max-width: 393px) {
    /* CSS for screens that are 393px or less will be put in this section */

    .contact_us_form_iframe {
        height: 840px;
        width: 90vw;
        margin-left: -15px;
    }
}
@media screen and (max-width: 357px) {
    /* CSS for screens that are 357px or less will be put in this section */

    .contact_us_form_iframe {
        height: 850px;
        width: 85vw;
        margin-left: -18px;
    }
}
