.carousel {
    position: relative;
    display: block;
    width: 100%;
}

.carousel_btn {
    position: absolute;
    z-index: 1;
}

.carousel_btn .prev_btn, .carousel_btn .next_btn {
    width: 3vw;
    height: 3vw;
    margin-left: 980%;
    border-radius: 50%;
    background-color: #a11c21;
    // background-color: #560000;
    outline: none;

    -webkit-box-shadow: 0px 0px 20px -1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 20px -1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 20px -1px rgba(0,0,0,0.75);
}

.next_arrow_btn, .prev_arrow_btn {
    // color: #d28616;
    color: #ffb105;
}

.carousel_btn .prev_btn:hover, .carousel_btn .next_btn:hover {
    background-color: #23262d;
    cursor: pointer;
}

// .carousel_btn .next_btn:hover .next_arrow_btn, .carousel_btn .prev_btn:hover .prev_arrow_btn {
//     color: #d28616;
// }

.carousel_btn .prev_btn {
    margin-top: 24vw;
}
.carousel_btn .next_btn {
    margin-top: 1vw;
}

.carousel_slide {
    clip-path: circle(60% at 78vw);
    transition: clip-path 1s;
}

.lrc_img {
    position: relative;
    display: block;
    width: 75%;
    float: right;
    height: 50vw;
}

.mUZZN {
    margin: 0!important;
}

// .carousel-control-prev {
//     margin-left: 400px;
//     background-color: brown;
// }

@media screen and (max-width: 950px) {
    /* CSS for screens that are 400px - 950px will be put in this section */
    .carousel {
        background-color: #a11c21;
    }

    .carousel_slide {
        clip-path: circle(75% at 50vw 10vw);
        transition: clip-path 1s;     
    }

    .carousel_btn .prev_btn {
        position: absolute;
        margin-left: 2vw;
        margin-top: 61vw;
        width: 6.5vw;
        height: 6.5vw;

    }
    .carousel_btn .next_btn {
        position: absolute;
        margin-left: 91vw;
        margin-top: 61vw;
        width: 6.5vw;
        height: 6.5vw;
    }

    .lrc_img {
        width: 100%;
        height: 70vw;
    }
}
