.destiny_instruction {
    padding-top: 160px;
    padding-bottom: 5%;
}

.destiny_instruction_header {
    margin-bottom: 35px;
}

.destiny_instruction_header h6 {
    font-size: 140%;
    margin-left: -225px;
    margin-bottom: -10px;
    font-weight: normal;
    color: #df8c08;  
}
.destiny_instruction_header h1 {
    font-size: 300%;
    color: #252424;
}

.destiny_instruction_iframe {
    width: 830px;
    height: 100vh;
}

@media screen and (max-width:850px) {
    /* CSS for screens that are 850px or less will be put in this section */

    .destiny_instruction_iframe {
        width: 100vw;
    }
}

@media screen and (max-width:425px) {
    /* CSS for screens that are 425px or less will be put in this section */

    .destiny_instruction_header h6 {
        font-size: 5vw;
        margin-left: -52vw;
        margin-bottom: -8px;
    }
    .destiny_instruction_header h1 {
        font-size: 11vw;
    }
}