.App {
  text-align: center;
  /* background-color: rosybrown; */
  
  animation-name: fadeIn;
  animation-duration: 1s;
}

html, body {
  scroll-behavior: smooth;
  height: 100%;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  
}

/* Track */
::-webkit-scrollbar-track {
  /* background-color: #f1f1f1 0.5;
  opacity: 0.5; */
  /* background: #a11c21; */
  background: #d38610;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  /* background: #888;  */
  background: #a11c21;
  /* background: #d38610; */
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
  border-radius: 10px;
}

#destiny_portal, #epic_portal {
  display: block;
  float: right;
  position: fixed;
  z-index: 4;
  margin-top: 26px;
  cursor: pointer;
}

#destiny_portal {
  right: 170px;
}

#epic_portal {
  right: 310px;
}

.roboto {
  font-family: 'Roboto', sans-serif;
}

/* body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.content {
  flex: 1;
} */

@media screen and (max-width:765px) {
  /* CSS for screens that are 765px or less will be put in this section */
  #destiny_portal, #epic_portal{
    animation-name: fadeIn;
    animation-duration: 1s;
    transform:scale(0.9, 0.9);
  }
}

@media screen and (max-width: 715px) {
  /* CSS for screens that are 715px or less will be put in this section */
  #destiny_portal, #epic_portal{
    display: none;
  }
}
