.lrc_faqs {
    padding-top: 160px;
    padding-bottom: 5%;
    overflow: hidden;
}

.lrc_faqs_header {
    margin-bottom: 35px;
}

.lrc_faqs_header h6 {
    font-size: 140%;
    margin-left: -75px;
    margin-bottom: -10px;
    font-weight: normal;
    color: #df8c08;  
}
.lrc_faqs_header h1 {
    font-size: 300%;
    color: #252424;
}

.lrc_faqs_iframe {
    width: 830px;
    height: 900px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.lrc_faqs_iframe_drive {
    display: none;
}

@media screen and (max-width:850px) {
    /* CSS for screens that are 850px or less will be put in this section */
    .lrc_faqs_iframe {
        display: none;
    }

    .lrc_faqs_iframe_drive {
        display: block;
        height: 130vw;
    }
}
