.epic_instruction {
    padding-top: 160px;
    padding-bottom: 5%;
    overflow: hidden;
}

.epic_instruction_header {
    margin-bottom: 35px;
}

.epic_instruction_header h6 {
    font-size: 140%;
    margin-left: -135px;
    margin-bottom: -10px;
    font-weight: normal;
    color: #df8c08;  
}
.epic_instruction_header h1 {
    font-size: 300%;
    color: #252424;
}

.epic_instruction_iframe {
    width: 830px;
    height: 1850px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.epic_instruction_iframe_drive {
    display: none;
}

@media screen and (max-width:850px) {
    /* CSS for screens that are 850px or less will be put in this section */
    .epic_instruction_iframe {
        display: none;
    }

    .epic_instruction_iframe_drive {
        display: block;
        height: 139vw;
    }
}
