@media screen and (max-width: 550px) {
    /* CSS for screens that are 550 pixels or less will be put in this section */
    #dis_short_logo {
        margin-top: 24px;
        margin-left: 0%;
        width: 70%;
        animation-name: fadeIn;
        animation-duration: 1s;
    }
}
