.lrc_policy {
    padding-top: 160px;
    padding-bottom: 5%;
    overflow: hidden;
}

.lrc_policy_header {
    margin-bottom: 35px;
}

.lrc_policy_header h1 {
    font-size: 350%;
    color: #252424;
}
.lrc_policy_header h6 {
    margin-left: 490px;
    margin-top: -10px;
    font-size: 180%;
    font-weight: normal;
    color: #df8c08;  
}

.lrc_policy_iframe {
    width: 830px;
    height: 1850px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.lrc_policy_iframe_drive {
    display: none;
}

@media screen and (max-width:850px) {
    /* CSS for screens that are 850px or less will be put in this section */
    .lrc_policy_iframe {
        display: none;
    }

    .lrc_policy_iframe_drive {
        display: block;
        height: 139vw;
    }
    
    .lrc_policy_header h1 {
        font-size: 7vw;
    }

    .lrc_policy_header h6 {
        font-size: 3vw;
        margin-left: 63vw;
    }
}
